import React from "react";
import HoverPerspectiviser from "../hover-perspectiviser";
import { mediaQuery } from "../../devices";
import styled from "styled-components";
// TODO: On icon hover: scale up, add drop shadow, and rotate/skew ("3d") based on mouse pointer location!
// TODO: Show skills on a grid

const SkillBadgeStyle = styled.div`
  margin-left: auto;
  margin-right: auto;
`;

const norm = {
  SkillBadgeIconContainer: styled.div``,
  SkillBadgeIcon: styled.img`
    width: 100px;
    height: 100px;
    transition: all 200ms;
    object-fit: contain;
    transition: all 200ms;
    -webkit-transition: all 200ms;
    -moz-transition: all 200ms;

    :hover {
      transform: scale(1.1);
      transition: all 200ms;
      -webkit-transition: all 200ms;
      -moz-transition: all 200ms;
    }

    @media ${mediaQuery.s} {
      width: 110px;
      height: 110px;
    }

    @media ${mediaQuery.l} {
      width: 130px;
      height: 130px;
    }
  `,
  SkillBadgeName: styled.p`
    text-align: center;
    margin: 5px 0 0 0;
    font-size: 1.2em;
    font-weight: 700;
    transition: all 200ms;
    -webkit-transition: all 200ms;
    -moz-transition: all 200ms;

    @media ${mediaQuery.s} {
      font-size: 1.2em;
    }

    @media ${mediaQuery.l} {
      font-size: 1.2em;
    }

    @media ${mediaQuery.xl} {
      font-size: 1.3em;
    }

    @media ${mediaQuery.xxl} {
      font-size: 1.3em;
    }
  `,
};

const add = {
  SkillBadgeIconContainer: styled.div``,
  SkillBadgeIcon: styled.img`
    width: 55px;
    height: 55px;
    transition: all 200ms;
    object-fit: contain;
    transition: all 200ms;
    -webkit-transition: all 200ms;
    -moz-transition: all 200ms;
    opacity: .8;
    filter: grayscale(25%);

    :hover {
      transform: scale(1.1);
      transition: all 200ms;
      -webkit-transition: all 200ms;
      opacity: .9;
      filter: grayscale(15%);

      -moz-transition: all 200ms;
    }

    @media ${mediaQuery.m} {
      width: 60px;
      height: 60px;
    }

    @media ${mediaQuery.l} {
      width: 70px;
      height: 70px;
    }

    @media ${mediaQuery.xl} {
      width: 80px;
      height: 80px;
    }
  `,
  SkillBadgeName: styled.p`
    text-align: center;
    margin: 5px 0 0 0;
    font-size: 0.8em;
    font-weight: 700;
    transition: all 200ms;
    -webkit-transition: all 200ms;
    -moz-transition: all 200ms;

    @media ${mediaQuery.m} {
      font-size: 0.9em;
    }

    @media ${mediaQuery.l} {
      font-size: 1em;
    }

    @media ${mediaQuery.xl} {
      font-size: 1.1em;
    }
  `,
};

export default function SkillBadge(props) {
  const { name, icon, key } = props;

  const {
    SkillBadgeIconContainer,
    SkillBadgeIcon,
    SkillBadgeName,
  } = props.additional ? { ...add } : { ...norm };

  return (
    <SkillBadgeStyle className="skill-badge" key={key}>
      <SkillBadgeIconContainer
        className={
          (props.additional ? "additional-" : "") + "skill-badge-icon-container"
        }
        id={name + "-p-container"}
      >
        {icon && (
          <SkillBadgeIcon
            src={icon}
            className={
              (props.additional ? "additional-" : "") + "skill-badge-icon"
            }
            alt={name}
          />
        )}
      </SkillBadgeIconContainer>

      <SkillBadgeName
        className={(props.additional ? "additional-" : "") + "skill-badge-name"}
      >
        {name}
      </SkillBadgeName>
    </SkillBadgeStyle>
  );
}
