import React from "react";
import UserInfo from "./side_nav/user_info";
import NavOpts from "./side_nav/nav_opts";
import styled from "styled-components";

const Nav = styled.div`
  display: grid;
  grid-template-rows: auto auto 100%;
  gap: 0.5rem;
  max-width: 300px;
`;

export default function SideNav(props) {
  return (
    <Nav>
      <UserInfo isMini={props.isMini} id="user-info" />

      <div>
        <NavOpts isMini={props.isMini} id="nav-opts" />
      </div>
    </Nav>
  );
}
