import React from "react";
import AboutMe from "./content/about_me";
import Projects from "./content/projects";
import WorkExperience from "./content/work_experience";
import FunFacts from "./content/fun_facts";
import OrgInvolvement from "./content/org_involvement";
import { mediaQuery } from "../devices";
import styled from "styled-components";

const ContentGrid = styled.div`
  display: grid;
  grid-template-rows: auto auto auto auto;
  justify-content: start;
`;

const SectionTitle = styled.h1`
  text-align: center;
  font-weight: 700;
  font-style: normal;
  margin: 0px;
  font-size: 1.5em;

  @media ${mediaQuery.s} {
    font-size: 2.7em;
  }

  @media ${mediaQuery.m} {
    text-align: start;
    font-size: 3em;
  }
`;

export default function Content() {
  return (
    <ContentGrid>
      <div>
        <div id="about-me">
          <SectionTitle> About Me </SectionTitle>
          <AboutMe />
        </div>
        <br />
      </div>

      <div>
        <div id="work-experience">
          <SectionTitle> Work Experience </SectionTitle>
          <WorkExperience />
        </div>
        <br />
      </div>

      <div>
        <div id="projects">
          <SectionTitle> Projects </SectionTitle>
          <Projects />
        </div>
        <br />
      </div>

      <div>
        <div id="orgs">
          <SectionTitle> Organizations </SectionTitle>
          <OrgInvolvement />
        </div>
        <br />
      </div>

      <div>
        <div id="misc">
          <SectionTitle> Fun Facts </SectionTitle>
          <FunFacts />
        </div>
      </div>
    </ContentGrid>
  );
}
