import React from "react";
import SkillBadge from "./skill_badge";
import CppSVG from "../svgs/cpp.svg";
import ReactSVG from "../svgs/react-2.svg";
import JsSVG from "../svgs/javascript.svg";
import SQLSVG from "../svgs/postgresql.svg";
import PHPSVG from "../svgs/php-1.svg";
import FigmaSVG from "../svgs/figma-1.svg";
import NodeSVG from "../svgs/nodejs-icon.svg";
import RubySVG from "../svgs/ruby.svg";
import GraphQLSVG from "../svgs/graphql.svg";
import TypeScript from "../svgs/typescript.svg";
import Flutter from "../svgs/flutter-logo.svg";
import { mediaQuery } from "../../devices";
import styled from "styled-components";

// TODO Maybe store info/points in an online DB. Then axios.get() info.
// Allowing for updating info w/o needing to mess with this anymore...

const skillInfo = [
  { name: "C++", icon: CppSVG },
  { name: "React", icon: ReactSVG },
  { name: "JavaScript", icon: JsSVG },
  { name: "SQL", icon: SQLSVG },
  { name: "PHP", icon: PHPSVG },
  { name: "Figma", icon: FigmaSVG },
  { name: "RESTful APIs", icon: "" },
];

const additionalSkillInfo = [
  { name: "Node.js", icon: NodeSVG },
  { name: "Ruby", icon: RubySVG },
  { name: "GraphQL", icon: GraphQLSVG },
  { name: "TypeScript", icon: TypeScript },
  { name: "Flutter", icon: Flutter },
  { name: "Agile Methodology", icon: "" },
];

const SectionSubtitle = styled.h2`
  text-align: center;
  font-weight: 500;
  margin: 0px 0px 5px 0px;
  font-size: 1.3em;
  color: rgba(0, 0, 0, 0.7);
  transition: all 200ms;
  -webkit-transition: all 200ms;
  -moz-transition: all 200ms;

  :hover {
    color: rgba(0, 0, 0, 0.9);
    transition: all 200ms;
    -webkit-transition: all 200ms;
    -moz-transition: all 200ms;
  }

  @media ${mediaQuery.s} {
    font-size: 2em;
  }

  @media ${mediaQuery.m} {
    text-align: start;
    font-size: 2em;
  }
`;

const SectionInfo = styled.p`
  text-align: center;
  margin: 0;
  font-size: 1.1em;

  @media ${mediaQuery.s} {
    font-size: 1.37em;
  }

  @media ${mediaQuery.m} {
    text-align: start;
    font-size: 1.37em;
  }
`;

const SkillList = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-template-rows: auto;
  justify-content: center;
  column-gap: 2rem;
  row-gap: 2rem;

  @media ${mediaQuery.s} {
    grid-template-columns: repeat(4, 110px);
    row-gap: 2.5rem;
    column-gap: 2.5rem;
  }

  @media ${mediaQuery.m} {
    grid-template-columns: auto auto auto auto;
    grid-template-rows: auto;
    justify-content: start;
    column-gap: 1.2rem;
    row-gap: 3rem;
  }

  @media ${mediaQuery.l} {
    grid-template-columns: repeat(5, 130px);
    row-gap: 2.5rem;
    column-gap: 2.5rem;
  }

  @media ${mediaQuery.xl} {
    grid-template-columns: repeat(6, 165px);
    row-gap: 2.5rem;
    column-gap: 2.5rem;
  }

  @media ${mediaQuery.xxl} {
    grid-template-columns: repeat(7, 155px);
    row-gap: 2.5rem;
    column-gap: 2.5rem;
  }
`;

const SkillListItem = styled.div``;

const AdditionalSkillList = styled.div`
  display: grid;
  grid-template-columns: repeat(5, 50px);
  grid-template-rows: auto;
  justify-content: center;
  column-gap: 1.6rem;
  row-gap: 1.6rem;

  @media ${mediaQuery.s} {
    column-gap: 1.7rem;
    row-gap: 1.7rem;
  }

  @media ${mediaQuery.m} {
    grid-template-columns: auto auto auto auto auto auto auto;
    grid-template-rows: auto;
    justify-content: start;
    column-gap: 1.8rem;
    row-gap: 1.8rem;
  }

  @media ${mediaQuery.l} {
    column-gap: 1.9rem;
    row-gap: 1.9rem;
  }
`;

const AdditionalSkillListItem = styled.div``;

const AboutMe = (props) => {
  return (
    <>
      <SectionSubtitle className="section-subtitle"> Hi! </SectionSubtitle>
      <SectionInfo className="section-info">
        My name is Fabian Gonzalez and I graduated from{" "}
        <b style={{ color: "rgb(240,80,35)" }}>
          The University of Texas Rio Grande Valley
        </b>{" "}
        with a B.Sc. in Computer Science in May 2020. üéì
      </SectionInfo>
      {/* <pre className="prettyprint">
                    { `SELECT Skills FROM Info;` }
            </pre> */}
      <br />

      <SectionSubtitle className="section-subtitle"> Skills </SectionSubtitle>
      <SkillList className="skill-list">
        {skillInfo.map((skill) => {
          return (
            <SkillListItem className="skill-list-item">
              {SkillBadge({
                name: skill.name,
                icon: skill.icon,
                additional: false,
                key: skill.name,
              })}
            </SkillListItem>
          );
        })}
      </SkillList>

      <br />

      <SectionSubtitle className="section-subtitle">
        {" "}
        Additional Skills{" "}
      </SectionSubtitle>
      <AdditionalSkillList className="additional-skill-list">
        {additionalSkillInfo.map((skill) => {
          return (
            <AdditionalSkillListItem className="additional-skill-list-item">
              {SkillBadge({
                name: skill.name,
                icon: skill.icon,
                additional: true,
                key: skill.name,
              })}
            </AdditionalSkillListItem>
          );
        })}
      </AdditionalSkillList>
    </>
  );
};

/* Could do a SQL statement that selects skills/fun from fabian in multiverse.universe.milkyway.earth.texas
 */
export default AboutMe;
