import React from "react";
import { FaTwitter, FaGithub, FaLinkedin } from "react-icons/fa";
import img from "../../me.jpg";
import styled from "styled-components";
import { mediaQuery } from "../../devices";

const icons = [
  { icon: FaGithub, link: "http://github.com/infocus7/" },
  { icon: FaLinkedin, link: "https://www.linkedin.com/in/fabiangonz98/" },
  { icon: FaTwitter, link: "https://twitter.com/fabiangonz98" },
];

const IconList = styled.ul`
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: none;

  @media ${mediaQuery.m} {
    display: block;
  }
`;

const IconListContainer = styled.div`
  margin: 0 auto;
  text-align: center;
`;

const Icon = styled.div`
  display: inline;
  margin-right: 12px;
`;

const IconLink = styled.a`
  color: rgba(0, 0, 0, 0.75);

  :hover {
    color: rgba(0, 0, 0, 0.9);
  }

  :visited {
    color: rgba(0, 0, 0, 0.75);
  }
`;

const InfoLayout = styled.div`
  display: grid;
  gap: 0rem;
  grid-template-columns: repeat(2, auto);
  justify-content: center;
  justify-items: center;
  vertical-align: center;
  align-items: center;

  @media ${mediaQuery.m} {
    grid-template-columns: none; /* Didn't realize rows stayed active */
    grid-template-rows: repeat(5, auto);
    justify-content: center;
    justify-items: center;
  }
`;

const OuterImg = styled.div`
  background: rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);

  @media ${mediaQuery.m} {
    width: 162px;
    height: 162px;
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
  }
`;

const InnerImg = styled.img`
  border-radius: 50%;
  width: 45px;
  height: 45px;

  @media ${mediaQuery.m} {
    width: 150px;
    height: 150px;
  }
`;

const InfoText = styled.div`
  display: grid;
  gap: 0rem;
  grid-template-rows: repeat(3, auto);
  justify-content: center;
  justify-items: center;
  vertical-align: center;
  align-items: center;
`;

const InfoName = styled.p`
  cursor: default;
  font-weight: 800;
  padding: 0px;
  padding: 0px;
  color: rgba(0, 0, 0, 0.8);
  font-size: 1.7rem;
  margin: 0px 0px 0px 2vw;
  align-self: center;

  @media ${mediaQuery.s} {
    font-weight: 800;
    margin: 0px 0px 0px 2vw;
    font-size: 2.3rem;
  }

  @media ${mediaQuery.m} {
    font-weight: 800;
    margin: 0px 0px 0px 2vw;
    font-size: 1.5rem;
    display: block;
    font-size: 1.35rem;
    margin: 4px 0px 0px 0px;
  }
`;

const InfoDegree = styled.p`
  margin: 0px;
  color: rgba(0, 0, 0, 0.7);
  padding: 0px;
  font-size: 1rem;
  font-weight: 600;

  @media ${mediaQuery.m} {
    font-size: 1rem;
    font-weight: 600;
    margin: 2px 0px 0px 0px;
    display: block;
  }
`;

const InfoPosition = styled.p`
  margin: 0px;
  color: rgba(0, 0, 0, 0.6);
  padding: 0px;
  font-size: 0.9rem;
  font-weight: 500;

  @media ${mediaQuery.m} {
    font-size: 0.9rem;
    font-weight: 500;
    margin: 2px 0px 6px 0px;
    display: block;
  }
`;

const Icons = (props) => {
  return (
    <IconListContainer>
      <IconList>
        {icons.map((i) => {
          return (
            <Icon>
              <IconLink href={i.link} target="_blank" rel="noopener noreferrer">
                <i.icon size="6.7%" />
              </IconLink>
            </Icon>
          );
        })}
      </IconList>
    </IconListContainer>
  );
};

export default function UserInfo(props) {
  return (
    <InfoLayout id="info-layout">
      <OuterImg className="outer-img">
        <InnerImg className="inner-img" src={img} alt="Fabian, the creator." />
      </OuterImg>

      {props.isMini ? (
        <InfoText id="info-text">
          <InfoName id="info-name">Fabi√°n Gonz√°lez</InfoName>

          <InfoDegree id="info-degree">B.Cs. in Computer Science</InfoDegree>

          <InfoPosition id="info-position">
            Software Engineer, Full Stack Developer
          </InfoPosition>
        </InfoText>
      ) : (
        <>
          <InfoName id="info-name">Fabi√°n Gonz√°lez</InfoName>

          <InfoDegree id="info-degree">B.Cs. in Computer Science</InfoDegree>

          <InfoPosition id="info-position">
            Software Engineer, Full Stack Developer
          </InfoPosition>
        </>
      )}

      <Icons id="info-icons" />
    </InfoLayout>
  );
}
